<div style={{ display: "flex", width: "100%" }}>
  <button
    onClick={() => setIsLogin(true)}
    style={{
      width: "50%",          // ocupă jumătate din container
      height: "48px",
      borderRadius: "0 8.721px 0 0", // rotunjim doar colțul exterior
      background: isLogin ? activeBackground : "#FFF",
      color: "#000",
      fontFamily: "Poppins, sans-serif",
      fontSize: "19.186px",
      fontWeight: 600,
      border: "none",
      cursor: "pointer",
    }}
  >
    Login
  </button>
  <button
    onClick={() => setIsLogin(false)}
    style={{
      width: "50%",          // ocupă cealaltă jumătate
      height: "48px",
      borderRadius: "8.721px 0 0 0", // rotunjim doar colțul exterior
      background: !isLogin ? activeBackground : "#FFF",
      color: "#000",
      fontFamily: "Poppins, sans-serif",
      fontSize: "19.186px",
      fontWeight: 600,
      border: "none",
      cursor: "pointer",
    }}
  >
    Sign Up
  </button>
</div>
